import { ElementInterface } from "./interfaces/ElementInterface";

export class Element implements ElementInterface {
  createdAt: string;
  id: string;
  isDeleted: boolean;
  isPublished: boolean;
  ownerId: number;
  updatedAt: string;
  updatedBy: number;
  [key: string]: any;
  schema?: string;

  constructor(data: ElementInterface, schema = '') {
    Object.assign(this, data);

    if (schema) {
      this.schema = schema;
    }
  }
}
