import {Repository} from "./Repository";
import {AxiosInstance} from "axios";
import {ElementInterface} from "../models/interfaces/ElementInterface";
import { Element } from "../models/Element";
import {QueryParametersInterface} from "./interfaces/QueryParametersInterface";

import { ElementConverter } from "./ElementConverter";


export class
ObjectRepository implements Repository{
    client: AxiosInstance;

    constructor(client: AxiosInstance) {
        this.client = client;
    }

    private applyConverter<T>(item: ElementInterface, schema: string, converter?: ElementConverter<T>) {
        if (converter) {
            return converter(item, schema);
        }

        return new Element(item, schema);
    }

    query<T = Element, C = ElementInterface>(schema: string, query: QueryParametersInterface = {}, converter?: ElementConverter<C> ,config: any = {}): Promise<T[]> {
        return this.client.post(`/objects/${schema}/query`, query, config)
            .then((obj) => {
                return (obj.data || []).map((item: ElementInterface) => this.applyConverter(item, schema, converter));
            });
    }

    count(schema: string, query: QueryParametersInterface = {}): Promise<number> {
      query = Object.assign({}, query,{
        take: 0,
        skip: 0
      });
      return this.client.post(`/objects/${schema}/query?count=true`, query)
              .then(({ headers }) => {
                  return parseInt(headers['x-appercode-totalitems'] || 0)
              });
    }

    find(schema: string, objectId: string) {
        return this.client.get<Element>(`/objects/${schema}/${objectId}`)
            .then((obj) => {
                const data = obj.data;
                return new Element(data);
            });
    }

    create(schema: string, data: any, config: any = {}) {
        return this.client.post(`/objects/${schema}`, data, config)
            .then(({data}) => {
                return new Element(data);
            })
    }

    delete(schema: string, ids: string[]) {
        return this.client.delete(`/objects/${schema}/batch`, {
            data: ids
        });
    }

}
