

import { defineComponent } from 'vue'
import s from './style.module.scss';

export default defineComponent({
  name: '',
  components: {

  },
  props: {
    value: {
      required: true,
      type: String
    },
    placeholder: {
      type: String
    }
  },
  data() {
    return {
      s,
    }
  },
  methods: {
    open(e: InputEvent) {

      const element = this.getElement()[0];
      //@ts-ignore
      element.showPicker();

      console.dir("show");

      // e.stopPropagation();
      // e.preventDefault();
    },
    getElement() {
      return (this.$el as HTMLElement).getElementsByTagName("input");
    }
  },
  computed: {
    model: {
        get() {
            return this.value;
        },
        set(value: string) {
            this.$emit('input', value);
        }
    }
  }
})

