import { Repository } from "./Repository";
import Axios, { AxiosInstance } from "axios";
import { File as AppercodeFile } from "../models/File";

export class FileRepository implements Repository {
  client: AxiosInstance;

  constructor(client: AxiosInstance) {
    this.client = client;
  }

  async create(
    file: File,
    name = "file",
    storage = "",
    uploadConfig: any = {}
  ): Promise<AppercodeFile> {
    return this.client
      .post<AppercodeFile>(
        `/files/?fileStore=` + storage,
        new AppercodeFile({
          name: name,
          shareStatus: "shared",
          rights: { READ: true },
          length: file.size
        })
      )
      .then(({ data }) => {
        return this.upload(data, file, uploadConfig);
      });
  }

  async upload(
    data: any,
    file: File,
    config: any = {}
  ): Promise<AppercodeFile> {
    const fd: FormData = new FormData();
    fd.append("file", file);
    config["headers"] = {
      "Content-Type": "multipart/form-data"
    };

    return this.client
      .post(`/files/` + data.id + `/upload`, fd, config)
      .then(() => {
        return new AppercodeFile(data);
      })
      .catch(error => {
        if (Axios.isCancel(error)) {
          this.delete(data.id);
        }
        throw error;
      });
  }

  async get(id: string): Promise<AppercodeFile> {
    return this.client.get<AppercodeFile>(`/files/` + id).then(({ data }) => {
      return new AppercodeFile(data);
    });
  }

  delete(id: string, markupAsDeleted: boolean = false) {
    return this.client.delete(`/files/${id}`, {
      params: {
        markupAsDeleted
      }
    });
  }

  download(id: string): Promise<Blob> {
    return this.client
      .get(`/files/${id}/download/`, {
        responseType: "blob"
      })
      .then(({ data }) => {
        return data;
      });
  }
}
