import profileColors from "../config/profileColors";

export function changeProtocol(link) {
  let currentProtocol = window.location.protocol;
  if (currentProtocol && link) {
    currentProtocol = currentProtocol + "//";
    link = link.replace("http://", currentProtocol);
  }
  return link;
}

export function isNetworkError(error) {
  return error.request && !error.response;
}

export function getProfileColor(id) {
  if (id) {
    const d = id[id.length - 1];

    return profileColors[parseInt(d, 16)];
  }
  return profileColors[0];
}

export function b64DecodeUnicode(str) {
  // Going backwards: from bytestream, to percent-encoding, to original string.
  return decodeURIComponent(
    atob(str)
      .split("")
      .map(function(c) {
        return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
      })
      .join("")
  );
}

export function scrollTo(element, to, duration) {
  if (duration < 0) return;
  var difference = to - element.scrollTop,
    perTick = (difference / duration) * 10;
  setTimeout(function() {
    element.scrollTop = element.scrollTop + perTick;
    if (element.scrollTop === to) return;
    scrollTo(element, to, duration - 10);
  }, 10);
}

export default {
  scrollTo,
  changeProtocol,
  isNetworkError,
  getProfileColor,
  b64DecodeUnicode
};
