import {Repository} from "./Repository";
import {AxiosInstance} from "axios";
import {User} from "../models/User";
import {UserProfile} from "../models/UserProfile";
import {OwnProfile} from "../models/OwnProfile";
import {UserProfileInterface} from "../models/interfaces/UserProfileInterface";
import {UserInterface} from "../models/interfaces/UserInterface";
import {QueryParametersInterface} from "./interfaces/QueryParametersInterface";
import {Session} from "../models/Session";

export class UserRepository implements Repository {
    client: AxiosInstance;

    constructor(client: AxiosInstance) {
        this.client = client;
    }

    ownProfile(): Promise<UserProfile|null> {
        return this.client.get<UserProfileInterface>(`/users/me/profile`)
            .then(({data}) => {
                return data ? new UserProfile(data) : null;
            });
    }

    saveOwnProfile(profile: UserProfile): Promise<UserProfile|null> {
        return this.client.put<UserProfileInterface>(`/users/me/profile`, new OwnProfile(profile))
            .then(({data}) => {
                return data ? new UserProfile(data) : null;
            });
    }

    profile(userId: number, include: string[] = []): Promise<UserProfile|null> {
        return this.client.post<UserProfileInterface[]>(`/objects/UserProfiles/query`, {
            take: 1,
            where: { userId: userId },
            include
        })
            .then(({data}) => {
                return data ? new UserProfile(data[0]) : null;
            });
    }

    profiles(query: QueryParametersInterface = {}, config = {}) {
        return this.client.post<UserProfileInterface[]>(`/objects/UserProfiles/query`, query, config)
            .then(({data}) => {
                return data.map(item => new UserProfile(item))
            })
    }

    profilesWithCount(query: QueryParametersInterface = {}, config = {}): Promise<{profiles: UserProfile[]; count: number}> {
        return this.client.post<UserProfileInterface[]>(`/objects/UserProfiles/query`, query, {
            ...config,
            params: {count: true}
        })
            .then(({data, headers}) => {
                return {profiles: data.map(item => new UserProfile(item)), count: parseInt(headers['x-appercode-totalitems'] || 0)}
            })
    }

    saveProfile(profile: UserProfile) {
        return this.client.put('/objects/UserProfiles/' + profile.id, profile);
    }

    user(id: number): Promise<User|null> {
        return this.client.get<UserInterface>('users/' + id)
            .then(({data}) => {
                return data ? new User(data) : null;
            });
    }

    saveUser(id: number, changes: UserInterface) {
        return this.client.put('/users/' + id, changes);
    }

    changePassword(id: number, password: string, oldPassword: string) {
        return this.client.put('/users/' + id + '/changePassword', {
            'oldPassword': oldPassword,
            'newPassword': password
        })
    }

    activity(ids: number[], activity: 'last' | 'first') {
        return this.client.post<Record<number, string>[]>(`/users/activity/query`, ids, {
            params: {
                activity
            }
        })
            .then(({data}) => {
                return data;
            })
    }

    loginAnonymous(config: any = {}) {
        return this.client.post(`/login/anonymous`, "", config)
            .then(({data}) => {
                return new Session(data);
            })
    }
}
