import { FileInterface } from "./interfaces/FileInterface";

export class File {
  id = undefined;
  length = undefined;
  name = "";
  isFile = true;
  parentId = undefined;
  ownerId = undefined;
  fileType = undefined;
  shareStatus = undefined;
  shareStatusTotal = undefined;
  rights = {};
  properties = undefined;
  createdAt = undefined;
  updatedAt = undefined;

  constructor(data: FileInterface) {
    Object.assign(this, data);
  }
}
