import { UserProfileInterface } from "./interfaces/UserProfileInterface";
import { getProfileColor } from "../../helpers/profileColor";

export class UserProfile {
  id = "";
  lastName = "";
  firstName = "";
  position = undefined;
  company = undefined;
  groupIds: string[] | undefined = undefined;
  photoFileId = undefined;
  email = "";
  phoneNumber = "";
  userId?: number = undefined;
  biography = "";
  [key: string]: any;

  constructor(data: UserProfileInterface) {
    Object.assign(this, {
      id: data.id ? data.id : "",
      lastName: data.lastName ? data.lastName : "",
      firstName: data.firstName ? data.firstName : "",
      position: data.position ? data.position : undefined,
      company: data.company ? data.company : undefined,
      groupIds: data.groupIds ? data.groupIds : undefined,
      photoFileId: data.photoFileId ? data.photoFileId : undefined,
      email: data.email ? data.email : "",
      phoneNumber: data.phoneNumber ? data.phoneNumber : "",
      userId: data.userId ? data.userId : undefined,
      biography: data.biography ?? "",
      cityId: data.cityId ?? ""
    });
  }

  get initials(): string {
    const firstName = this.firstName && this.firstName[0],
      lastName = this.lastName && this.lastName[0];
    return [firstName || "", lastName || ""].filter(item => item).join("");
  }

  get fullName(): string {
    return [this.firstName, this.lastName].filter(item => item).join(" ");
  }

  get job(): string {
    return [this.company, this.position].filter(item => item).join(", ");
  }

  profilePhotoLink(baseUrl: string, backend: string, width = 88): string {
    return this.photoFileId
      ? baseUrl +
          `${backend}/images/${this.photoFileId}/download?width=${width}`
      : "";
  }

  get profileColor(): string {
    const id = (this as any).profileId || this.id || "";
    return getProfileColor(id);
  }

  setFirstEntranceStatus(value = false) {
    this.firstEntranceStatus = value;
  }
}
